.dropdown {
  position: relative;
  display: inline-block; }
  .dropdown__toggle {
    padding-right: 50px; }
    .dropdown__toggle::after {
      position: absolute;
      content: '';
      top: 43%;
      right: 8%;
      display: inline-block;
      border-top: 8px solid;
      border-right: 8px solid transparent;
      border-bottom: 0;
      border-left: 8px solid transparent; }
  .dropdown__list {
    margin: 0;
    padding: 0;
    list-style: none;
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 10;
    display: none;
