.accordion {
  margin: 0 auto; }
  .accordion__header {
    width: 100%;
    margin: 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1); }
    .accordion__header--active .accordion__button {
      color: #3862e0;
      background-color: #e6e6e6; }
      .accordion__header--active .accordion__button::after {
        transform: rotate(-180deg); }
  .accordion__button {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    padding: 20px 40px;
    color: #404040;
    font: inherit;
    font-weight: bold;
    text-align: left;
    vertical-align: middle;
    border: none;
    background-color: white;
    cursor: pointer;
    transition: color 300ms; }
    .accordion__button::after {
      flex-shrink: 0;
      content: '';
